// buttons

@mixin mkdfButtonDefaultStyle() {
    font-family: inherit;
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: auto;
    outline: none;
    font-size: 14px;
    line-height: 1em;
    letter-spacing: 0.25em;
    font-weight: 700;
    text-transform: uppercase;
    box-sizing: border-box;
    margin: 0;

    @include mkdfTransition(color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out);
}

@mixin mkdfButtonSimple($important:null) {
    background-color: transparent $important;
    border: 0 $important;
    color: $first-main-color $important;
    padding: 0 !important;
    vertical-align: middle $important;

    .mkdf-btn-text,
    .button-text {
        position: relative;
        display: inline-block $important;
        vertical-align: middle $important;

        &:before,
        &:after {
            content: "";
            position: absolute;
            bottom: -2px;
            left: 0;
            width: 100%;
            height: 1px;
            background-color: currentColor;
        }

        &:before {
            transform-origin: right;

            @include mkdfTransition(all .2s ease-in 0s);
        }

        &:after {
            transform-origin: left;

            @include mkdfTransform(scale(0, 1));
            @include mkdfTransition(all .25s ease-out .25s);
        }

        &:hover {
            &:before {
                @include mkdfTransform(scale(0, 1));
            }

            &:after {
                @include mkdfTransform(scale(1, 1));
            }
        }
    }
}

@mixin mkdfButtonTransparentColor($important:null) {
    color: $default-text-color $important;
    background-color: transparent $important;
}

@mixin mkdfButtonTransparentHoverColor($important:null) {
    color: $first-main-color $important;
    background-color: transparent $important;
}

@mixin mkdfButtonSolidColor($important:null) {
    color: #fff $important;
    background-color: $default-text-color $important;
    border: 1px solid transparent $important;
}

@mixin mkdfButtonSolidHoverColor($important:null) {
    color: #fff $important;
    background-color: $first-main-color $important;
}

@mixin mkdfButtonOutlineColor($important:null) {
    color: $default-text-color $important;
    background-color: transparent $important;
    border: 1px solid $default-text-color $important;
}

@mixin mkdfButtonOutlineHoverColor($important:null) {
    color: #fff $important;
    background-color: $first-main-color $important;
    border-color: $first-main-color $important;
}

@mixin mkdfButtonSmallParams($important:null) {
    font-size: 12px $important;
    padding: 14px 55px $important;
}

@mixin mkdfButtonMediumParams($important:null) {
    padding: 18px 70px $important;
}

@mixin mkdfButtonLargeParams($important:null) {
    font-size: 16px $important;
    padding: 20px 74px $important;
}

@mixin mkdfButtonHugeParams($important:null) {
    display: block $important;
    font-size: 18px $important;
    padding-bottom: 22px $important;
    padding-top: 22px $important;
    text-align: center $important;
    width: 100% $important;
}

@mixin mkdfPlaceholder {
    &::-webkit-input-placeholder {
        @content
    }

    &:-moz-placeholder {
        @content;

        opacity: 1;
    }

    &::-moz-placeholder {
        @content;

        opacity: 1;
    }

    &:-ms-input-placeholder {
        @content
    }
}

/* common mixins - end */

/* mixins styles - start */

%input-style {
    background-color: transparent;
    border: 1px solid $default-text-color;
    border-radius: 0;
    box-sizing: border-box;
    color: $default-text-color;
    cursor: inherit;
    font-family: inherit;
    font-size: 13px;
    font-weight: inherit;
    height: $input-height;
    line-height: #{$input-height - 2px};
    margin: 0 0 $input-margin;
    outline: 0;
    padding: 0 $input-padding;
    width: 100%;

    @include mkdfTransition(border-color 0.2s ease-in-out);

    @include mkdfPlaceholder {
        color: inherit;

        @include mkdfTransition(color 0.2s ease-in-out);
    }

    -webkit-appearance: none;

    &:focus {
        color: $default-text-color;

        @include mkdfPlaceholder {
            color: transparent;
        }
    }
}

/* mixins styles - end */

// heading mixins start
@mixin mkdfParagraph($important:null) {
    font-size: 16px $important;
    font-weight: 300 $important;
    letter-spacing: normal $important;
    line-height: 1.75em $important;
    text-transform: none $important;
}

@mixin mkdfGlobalHeadingStyle() {
    color: $default-text-color;

    a {
        @include mkdfTypographyLayout();

        &:hover {
