//flexbox mixins - start

@mixin mkdfFlexContainer($flex-direction: row, $flex-wrap: nowrap, $justify-content: flex-start, $align-items: stretch, $align-content: stretch ) {
    display: flex;
    flex-direction: $flex-direction;
